<template>
    <div class="tab" v-show="!isLoginPage">
        <router-link tag="div" class="tab-item index" to="/index">
            <span>首页</span>
        </router-link>
        <router-link tag="div" class="tab-item features" to="/features">
            <span>店家功能</span>
        </router-link>
        <router-link tag="div" class="tab-item purchase" to="/purchase">
            <span>原料采购</span>
        </router-link>
        <router-link tag="div" class="tab-item mine" to="/mine">
            <span>我的</span>
        </router-link>
    </div>
</template>

<script type="text/ecmascript-6">
  import {mapGetters} from 'vuex'

  export default {
    data () {
      return {

      }
    },
    computed: {
      ...mapGetters([
        'isLoginPage'
      ])
    },
    components: {

    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
    @import "~common/stylus/variable"
    .tab
      display flex
      position fixed
      left 0
      right 0
      bottom 0
      height 50px
      background $color-tab-background
      font-size $font-size-small
      .tab-item
        display flex
        flex 1
        justify-content center
        color $color-tab-text
        span
          position absolute
          top 30px
          height 14px
          display flex
          align-items center
        &:before
          position absolute
          background-size contain
          height 19px
          top 8px
          content ''
        &.index:before
          background url("~common/image/tab/index.png") no-repeat
          width 22px
          background-size contain
        &.features:before
          background url("~common/image/tab/features.png") no-repeat
          width 20px
          background-size contain
        &.purchase:before
          background url("~common/image/tab/purchase.png") no-repeat
          width 19px
          background-size contain
        &.mine:before
          background url("~common/image/tab/mine.png") no-repeat
          width 20px
          background-size contain
        &.router-link-active
          color $color-tab-text-active
          &.index:before
            background url("~common/image/tab/index-active.png") no-repeat
            width 22px
            background-size contain
          &.features:before
            background url("~common/image/tab/features-active.png") no-repeat
            width 20px
            background-size contain
          &.purchase:before
            background url("~common/image/tab/purchase-active.png") no-repeat
            width 19px
            background-size contain
          &.mine:before
            background url("~common/image/tab/mine-active.png") no-repeat
            width 20px
            background-size contain
</style>



