import Vue from 'vue'
import Router from 'vue-router'

// 登录页 --- BEGIN
// import Login from 'components/login/login'
const Login = () => import('components/login/login')
// 登录页 --- END

// 首页 --- BEGIN
// import Index from 'components/index/index'
// import IndexDetail from 'components/index/detail'
const Index = () => import('components/index/index')
const IndexDetail = () => import('components/index/detail')
// 首页 --- END

// 店家功能---BEGIN
// import Features from 'components/features/features'
// import FeaturesQRcode from 'components/features/qrcode'
// import FeaturesUndoRecord from 'components/features/undorecord'
// import FeaturesTradeRecord from 'components/features/traderecord'
// import FeaturesMembers from 'components/features/members'
// import FeaturesPayAccount from 'components/features/payaccount'
// import FeaturesStatement from 'components/features/statement'
const Features = () => import('components/features/features')
const FeaturesQRcode = () => import('components/features/qrcode')
const FeaturesUndoRecord = () => import('components/features/undorecord')
const FeaturesTradeRecord = () => import('components/features/traderecord')
const FeaturesMembers = () => import('components/features/members')
const FeaturesPayAccount = () => import('components/features/payaccount')
const FeaturesStatement = () => import('components/features/statement')
// 店家功能---END

// 原料采购---BEGIN
// import Purchase from 'components/purchase/purchase'
// import MaterialIndex from 'components/purchase/materialindex'
// import MyOrder from 'components/purchase/myorder'
// import ShoppingCart from 'components/purchase/shoppingcart'
// import DistributeOrder from 'components/purchase/distributeorder'
// import Inventory from 'components/purchase/inventory'
const Purchase = () => import('components/purchase/purchase')
const PurchaseMaterialIndex = () => import('components/purchase/materialindex')
const PurchaseMyOrder = () => import('components/purchase/myorder')
const PurchaseShoppingCart = () => import('components/purchase/shoppingcart')
const PurchaseDistributeOrder = () => import('components/purchase/distributeorder')
const PurchaseInventory = () => import('components/purchase/inventory')
// 原料采购---END

// 我的---BEGIN
// import Mine from 'components/mine/mine'
// import MineBaseInfo from 'components/mine/baseinfo'
// import MineQRcode from 'components/mine/qrcode'
// import MineIRecommend from 'components/mine/irecommend'
// import MinePassword from 'components/mine/password'
const Mine = () => import('components/mine/mine')
const MineBaseInfo = () => import('components/mine/baseinfo')
const MineQRcode = () => import('components/mine/qrcode')
const MineIRecommend = () => import('components/mine/irecommend')
const MinePassword = () => import('components/mine/password')
// 我的---END

Vue.use(Router)

export default new Router({
  routes: [
    // {
    //   path: '*',
    //   redirect: '/login'
    // },
    {
      path: '/',
      redirect: '/login'
    },
    {
      path: '/login',
      component: Login
    },
    {
      path: '/index',
      component: Index,
      children: [
        {
          path: ':id',
          component: IndexDetail
        }
      ]
    },
    {
      path: '/features',
      component: Features,
      children: [
        {
          path: 'qrcode',
          component: FeaturesQRcode
        },
        {
          path: 'undorecord',
          component: FeaturesUndoRecord
        },
        {
          path: 'traderecord',
          component: FeaturesTradeRecord
        },
        {
          path: 'members',
          component: FeaturesMembers
        },
        {
          path: 'payaccount',
          component: FeaturesPayAccount
        },
        {
          path: 'statement',
          component: FeaturesStatement
        }
      ]
    },
    {
      path: '/purchase',
      component: Purchase,
      children: [
        {
          path: 'materialindex',
          component: PurchaseMaterialIndex
        },
        {
          path: 'myorder',
          component: PurchaseMyOrder
        },
        {
          path: 'shoppingcart',
          component: PurchaseShoppingCart
        },
        {
          path: 'distributeorder',
          component: PurchaseDistributeOrder
        },
        {
          path: 'inventory',
          component: PurchaseInventory
        }
      ]
    },
    {
      path: '/mine',
      component: Mine,
      children: [
        {
          path: 'baseinfo',
          component: MineBaseInfo
        },
        {
          path: 'qrcode',
          component: MineQRcode
        },
        {
          path: 'irecommend',
          component: MineIRecommend
        },
        {
          path: 'password',
          component: MinePassword
        },
      ]
    }
  ]
})



