<template>
  <div id="app">
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
      <tab></tab>
  </div>
</template>

<script type="text/ecmascript-6">
  import Tab from 'components/tab/tab'

  export default {
    name: 'App',
    data () {
      return {

      }
    },
    created () {
      // 暂时先这样，后期在实现具体功能的时候，在main.js中采用router.beforeEach
      // router.beforeEach((to, from, next) => {
      //   // ...
      // })
      // this.checkLogin()
    },
    methods: {
      checkLogin() {
        // 如果登录状态存在，则跳转到登录后的页面，否则停留在登录页面
        this.$router.push({
          path: 'index'
        })
      }
    },
    components: {
      Tab
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  #app
    width 100%
    height 100%
</style>



